import { LOAD_PROBLEM_SCOREBOARD } from '../consts/actions';
import IScoreboardAction, {
    IProblemDescription, IProblemScoreboardTeam,
    IScoreboardFromServer
} from '../models/actions/scoreboard';
import { ThunkAction } from 'redux-thunk';
import IStore from '../models/store';
import { Dispatch } from 'redux';
import { createSetErrorAction } from './app';
import { sendGetRequest } from '../utils/sendRequest';
import { problemsScoreboard } from '../consts/api';

const createLoadProblemScoreboardAction = (scoreboard: {
    problemDescription: IProblemDescription[];
    teams: IProblemScoreboardTeam[];
    frozenAt?: Date;
}) => ({
    type: LOAD_PROBLEM_SCOREBOARD,
    payload: scoreboard,
})


export function loadProblemScoreboard():
    ThunkAction<Promise<any>, IStore, unknown, IScoreboardAction> {
    return async (dispatch: Dispatch) => {
        try {
            const result: IScoreboardFromServer = await sendGetRequest(problemsScoreboard)
            
            dispatch(createLoadProblemScoreboardAction({
                problemDescription: result.problems,
                frozenAt: result.frozenAt ? new Date(result.frozenAt) : undefined,
                teams: result.teams,
            }))
        } catch (e) {
            dispatch(createSetErrorAction(e))
        }
    }
}
