import React from 'react';
import styles from './styles.module.css';
import './flag-icons.css';
import Team from '../../models/Team';
import { IProblemDescription, IProblemScoreboardTeam } from '../../models/actions/scoreboard';
import countriesJson from '../../consts/countries.json';
import ICountry from '../../models/Country';
import DateTime from '../DateTime';
import { formatter } from "../DateTime/DateTime";

interface ScoreboardPageProps {
    team: Team | null;
    frozenAt?: Date;
    scoreByTeams: IProblemScoreboardTeam[] | null;
    problems: IProblemDescription[] | null;
    loadProblemScoreboard: () => void;
}

enum SortDirection {
    increase = 'increase',
    decrease = 'decrease',
}

interface ScoreboardPageState {
    sortBy: string;
    sortDirection: SortDirection;
}

const countries = countriesJson as ICountry[];

export default class ScoreboardPage extends React.Component<ScoreboardPageProps, ScoreboardPageState> {
    state = {
        sortBy: 'general',
        sortDirection: SortDirection.decrease,
    }

    componentDidMount() {
        this.props.loadProblemScoreboard();
    }

    render() {
        const { sortBy, sortDirection } = this.state;
        const { team, scoreByTeams } = this.props;

        const teamsOrder = scoreByTeams && [...scoreByTeams].sort(sortElements(sortBy, sortDirection));

        return (
            <div>
                <h2>Scoreboard{ team && ': ' + team.teamName }</h2>

                <table className={ styles.table }>
                    <thead>
                    { this.renderHead() }
                    { teamsOrder && teamsOrder
                        .map((team: IProblemScoreboardTeam, i: number) => ({ team, i }))
                        .filter(tuple => tuple.team.team.teamId === team?.teamId && tuple.i > 5)
                        .map(tuple => this.renderRow(tuple.team, tuple.i))
                    }
                    </thead>
                    <tbody>
                    { teamsOrder && teamsOrder.map(this.renderRow) }
                    </tbody>
                </table>
            </div>
        )
    }

    renderHead() {
        const { problems } = this.props;
        const { sortBy, sortDirection } = this.state;
        const hasProblems = problems && problems.length > 0;
        const rowSpan = hasProblems ? 2 : 1;

        const buttonClass = `${ styles.sortButton } ${ sortBy === 'general'
            ? sortDirection === SortDirection.decrease ? styles.sortDown : styles.sortUp
            : '' }`

        return (
            <>
            <tr>
                <th style={ { width: 40 } } rowSpan={ rowSpan }>#</th>
                <th style={ { width: 40 } } rowSpan={ rowSpan }>&nbsp;</th>
                <th rowSpan={ rowSpan } className={ styles.alignLeft }>Team</th>
                { problems && problems.length &&
                <th colSpan={ problems.length } className={ styles.problemsHeader }>Solved Problems</th>
                }
                <th style={ { width: 70 } }
                    className={ styles.score }
                    rowSpan={ rowSpan }>
                    <button className={ buttonClass }
                            onClick={ this.changeSorted('general') }>
                        Score
                    </button>
                </th>
                <th rowSpan={ rowSpan }>Earned At</th>
            </tr>
            { problems && problems.length &&
            <tr>
                { problems.map(this.cellForProblem) }
            </tr>
            }
            </>
        )
    }

    cellForProblem = (problem: IProblemDescription) => {
        const { sortBy, sortDirection } = this.state;
        let text = problem.problemId;
        if (text === 'request') {
            text = 'R';
        }
        if (text === 'submission') {
            text = 'T';
            problem.description = 'Made submission passed the test'
        }
        if (text.startsWith('tutorial')) {
            text = text.replace('tutorial:', '');
        }
        const buttonClass = `${ styles.sortButton } ${ sortBy === problem.problemId
            ? sortDirection === SortDirection.decrease ? styles.sortDown : styles.sortUp
            : '' }`

        return (
            <th key={ problem.problemId }
                className={ styles.alignRight }
                title={ problem.description }>
                <button onClick={ this.changeSorted(problem.problemId) }
                        className={ buttonClass }>
                    { text }
                </button>
            </th>
        )
    }

    renderRow = (scoreboardTeam: IProblemScoreboardTeam, index: number) => {
        const { team, problems } = this.props;
        if (!problems) {
            return null;
        }

        const teamInfo = scoreboardTeam.team
        const itemCountry = teamInfo.customData.country;
        const country = countries.find(c => c.value === itemCountry);
        return (
            <tr key={ index } className={ team?.teamId === teamInfo.teamId ? styles.activeTeam : '' }>
                <td className={ styles.alignCenter }>{ index + 1 }</td>
                <td>
                    <span className={ `flag-icons ${ country?.shortValue }` }
                          title={ country?.text } />
                </td>
                <td>{ teamInfo.teamName }</td>
                { problems.map(p =>
                    <td key={ p.problemId }
                        className={ styles.problemScore }
                        title={ p.problemId in scoreboardTeam.problems
                            ? `Solved at ${ formatter.format(new Date(scoreboardTeam.problems[p.problemId].solvedAt)) }` : undefined }>
                        { p.problemId in scoreboardTeam.problems
                            ? scoreboardTeam.problems[p.problemId].score
                            : <span className={ styles.lighten }>¬∑</span> }
                    </td>
                ) }
                <td className={ styles.score }>{ scoreboardTeam.score }</td>
                <td className={ styles.alignRight }><DateTime dateStr={ scoreboardTeam.solvedAt }/>
                </td>
            </tr>
        )
    }

    changeSorted = (sortBy: string) => () => {
        if (sortBy === this.state.sortBy) {
            const sortDirection = this.state.sortDirection === SortDirection.decrease
                ? SortDirection.increase
                : SortDirection.decrease
            this.setState({ sortDirection })
            return;
        }
        this.setState({ sortBy, sortDirection: SortDirection.decrease })
    }
}

function sortElements(sortBy: string, sortDirection: SortDirection) {
    return (a: IProblemScoreboardTeam, b: IProblemScoreboardTeam) => {
        let aScore, bScore;
        if (sortBy === 'general') {
            aScore = { score: a.score, solvedAt: a.solvedAt };
            bScore = { score: b.score, solvedAt: b.solvedAt };
        } else {
            const aProblem = a.problems[sortBy];
            const bProblem = b.problems[sortBy];
            aScore = aProblem || { score: 0, solvedAt: new Date(0) }
            bScore = bProblem || { score: 0, solvedAt: new Date(0) }
        }

        if (sortDirection === SortDirection.decrease) {
            if (aScore.score > bScore.score) return -1;
            if (aScore.score < bScore.score) return 1;
            if (aScore.solvedAt < bScore.solvedAt) return -1;
            if (aScore.solvedAt > bScore.solvedAt) return 1;
            return 0;
        }

        if (aScore.score < bScore.score) return -1;
        if (aScore.score > bScore.score) return 1;
        if (aScore.solvedAt > bScore.solvedAt) return -1;
        if (aScore.solvedAt < bScore.solvedAt) return 1;
        return 0;
    }
}
