import { connect } from 'react-redux';
import ScoreboardPage from '../components/ScoreboardPage';
import IStore from '../models/store';
import { ThunkDispatch } from 'redux-thunk';
import { AnyAction } from 'redux';
import { loadProblemScoreboard } from '../actions/scoreboard';

const mapState = (store: IStore) => {
    return {
        team: store.team.team,
        scoreByTeams: store.scoreboard.teams,
        frozenAt: store.scoreboard.frozenAt,
        problems: store.scoreboard.problemDescription,
    }
}

const mapDispatch = (dispatch: ThunkDispatch<IStore, unknown, AnyAction>) => {
    return {
        loadProblemScoreboard: () => dispatch(loadProblemScoreboard()),
    }
}


export default connect(mapState, mapDispatch)(ScoreboardPage);
